import React from 'react';

import ContactCom from '../../components/contact_comp/contact.comp';

import './contact.style.scss';

class Contact extends React.Component{
    constructor(){
        super()

        this.state = {
            links: [
                {
                    url:"https://www.linkedin.com/in/matas-jarilinas-565949184",
                    img: "https://icongr.am/devicon/linkedin-plain-wordmark.svg",
                    name: "Linked.in",
                    id: 1
                }
            ]
        }
    }

    render(){
        return(
            <div className="contact">
                <h1>Contact</h1>
                <div className="content">
                    {this.state.links.map(link => <ContactCom key={link.id} name={link.name} url={link.url} img={link.img} />)}
                </div>
            </div>
        )
    }
}

export default Contact;